import React, { Fragment } from 'react';
import GatsbyLink from 'gatsby-link';
import styled from '@emotion/styled/macro';
import { Flex, Button } from 'rebass';


const PaginationWrap = styled(Flex)`
  border: 1px solid #e6e6e6;
  border-left: 0;
  border-right: 0;
`

const PageNav = styled(Button)`
  border-radius: 0;
`

const Pagination = props => {
  const { numPages, currentPage, shopifyThemePath } = props;
  const showNumbers = 2;

  let prevLink = false;
  let nextLink = false;

  if (currentPage !== 1) {
    prevLink = shopifyThemePath + '/' + (currentPage - 1);
  }
  if (currentPage === 2) {
    prevLink = shopifyThemePath;
  }
  if (currentPage !== numPages) {
    nextLink = shopifyThemePath + '/' + (currentPage + 1);
  }

  return (
    <Fragment>
      <PaginationWrap justifyContent="center" fontSize={['3vw', '1.5vw', '1vw']}>
        {prevLink ? (
          <PageNav
            as={GatsbyLink}
            to={prevLink}
            width={['30vw', '14vw', '12vw']}
            mr='auto'
            p={['4vw', '2vw']}
            sx={{ letterSpacing: ['.4vw', '.2vw'] }}
            rel="prev"
            variant="pagination"
          >
            Previous
          </PageNav>
        ) : (
          <PageNav
            width={['30vw', '14vw', '12vw']}
            mr='auto'
            p={['4vw', '2vw']}
            sx={{ letterSpacing: ['.4vw', '.2vw'] }}
            rel="next"
            variant="pagination"
            opacity='0.6'
          >
            Previous
          </PageNav>
        )}

        {Array.from({ length: numPages }).map((_, i) => {
          let pageNum = i + 1;

          if (
            pageNum >= Math.max(1, currentPage - showNumbers) &&
            pageNum <= Math.min(currentPage + showNumbers, numPages)
          ) {
            if (pageNum === currentPage) {
              return (
                <PageNav mx='auto' p={['4vw', '2vw']} variant="pagination-active" key={pageNum} sx={{ borderTop: '2px solid', borderBottom: '2px solid', fontWeight: 'bold' }}>
                  {pageNum}
                </PageNav>
              );
            } else {
              let link;
              pageNum === 1
                ? (link = shopifyThemePath)
                : (link = shopifyThemePath + '/' + pageNum);
              return (
                <PageNav
                  as={GatsbyLink}
                  to={link}
                  mx='auto'
                  p={['4vw', '2vw']}
                  variant="pagination"
                  key={pageNum}
                >
                  {pageNum}
                </PageNav>
              );
            }
          } else {
            return '';
          }
        })}

        {nextLink ? (
          <PageNav
            as={GatsbyLink}
            to={nextLink}
            width={['30vw', '14vw', '12vw']}
            ml='auto'
            p={['4vw', '2vw']}
            sx={{ letterSpacing: ['.4vw', '.2vw'] }}
            rel="next"
            variant="pagination"
          >
            Next
          </PageNav>
        ) : (
          <PageNav
            width={['30vw', '14vw', '12vw']}
            ml='auto'
            p={['4vw', '2vw']}
            sx={{ letterSpacing: ['.4vw', '.2vw'] }}
            rel="next"
            variant="pagination"
            opacity='0.6'
          >
            Next
          </PageNav>
        )}
      </PaginationWrap>
    </Fragment>
  );
};

export default Pagination;
